type Props = {};

const CardTitle = styled(Card.Title)`
  font-variant: all-small-caps;
  font-size: x-large;
  font-weight: bold;
`;

type CardProps = {
  children: Function,
  title: String,
  src: String,
};

const TitleRow = styled(Row)`
  text-align: center;
  margin: auto;
`;

const ProgramDeck = styled(CardDeck)`
  max-width: 1500px;
  margin: auto;
`;

const StyledCard = styled(Card)`
  min-width: 300px;
  max-width: 100vw;
  margin-top: 1em;
`;

const CardText = styled(Card.Text)`
  font-size: large;
`;

class ProgramCard extends Component<CardProps> {
  render() {
    const {children, title, src} = this.props;

    return (
      <StyledCard>
        <Card.Img variant="top" as={Image} src={src} />
        <Card.Body>
          <CardTitle>{title}</CardTitle>
          <CardText>{children}</CardText>
        </Card.Body>
        <Card.Footer>
          <Flex
            align="center"
            verticalAlign="center"
            css={{
              paddingTop: 40,
              paddingBottom: 20,
              flexWrap: 'wrap',
              justifyContent: 'center',

              [media.greaterThan('xlarge')]: {
                paddingTop: 65,
              },
            }}>
            <CTAButton to="/mitmachen" type="cta">
              Mitgestalten
            </CTAButton>
          </Flex>
        </Card.Footer>
      </StyledCard>
    );
  }
}

